import React, { Component } from "react";
import "./charpter.scss";
import { CSSTransition, TransitionGroup } from "react-transition-group";
import {  Slider } from 'antd-mobile'
import ReactMarkdown from 'react-markdown/with-html'
import axios from "axios"
let ch = [
  {
    title: "第一章 地理风俗",
    id: 0,
    essay: []
  },
  {
    title: "第二章 名人贤士",
    id: 1,
    essay: []
  },
  {
    title: "第三章 民间艺术",
    id: 2,
    essay: []
  },
  {
    title: "第四章 旅游景点",
    id: 3,
    essay: []
  },
  {
    title: "第五章 教育基地",
    id: 4,
    essay: []
  },
  {
    title: "第六章 潮州名片",
    id: 5,
    essay: []
  },
  {
    title: "第七章 后记",
    id: 6,
    essay: []
  }
];
const colorlist = [
  {
    color: '#c1b694',
    bgColor: '#c1b694',
    fontColor: "#000"
  },
  {
    color: 'gray',
    bgColor: 'gray',
    fontColor: "#ccc"
  },
  {
    color: '#c1ae96',
    bgColor: '#c1ae96',
    fontColor: "#000"
  },
  {
    color: '#c1bd96',
    bgColor: '#c1bd96',
    fontColor: "#000"
  },
  {
    color: '#d1b48e',
    bgColor: '#d1b48e',
    fontColor: "#000"
  },
  {
    color: '#6d6e87',
    bgColor: '#6d6e87',
    fontColor: "#000"
  }
]
class Charpter extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentId: "hidden",
      content:"",
      startX: 0,
      showDirect: false,
      isSwitch: true, //true状态下表示菜单栏关闭
      colorIndex: 0,
      isLang: true, //字体菜单默认关闭状态
      fontStyle: true, //true表示是微软雅黑
      fontInitclientX: 0,//字体设置的初始化位置
      fontPercent: '20%',
      lightInitclientX: 0,//亮度
      lightLastclientX: 0,
      lightPercent: '20%',
      arrow: false, //false 代表箭头👇
      isFirst:true,
      title:''
      // isTogether:false //false 代表是都没有开 true代表其中一个状为开启的
      //四个行为控制一个状态
    };
  }
  render() {
    return (
      <div className="reading" ref="reading">
        <div>
          {/* <h2 className="readCharpter">第一章 地理风俗</h2> */}
          <p className="title">{this.state.title}</p>
          
          <div className="readContent" ref="readContent">
          <ReactMarkdown source={this.state.content}  escapeHtml={false} />
          </div>
        </div>
        <div className="setMenu" ref="setMenu">
          <div className="setColor" >
            <ul>
              <li className="setIcon">
              </li>
              {
                colorlist.map((item, index) => {
                  return (
                    <li key={index}
                      className={index === this.state.colorIndex ? 'current' : ''}
                      onClick={this.changeColor.bind(this, index, item)}
                    >
                      <div className="bg"></div>
                      <div className="circle"
                        style={{ background: item.color }}
                      ></div>
                    </li>
                  )
                })
              }
            </ul>
          </div>
          <div className="setLight">
            <span className="light"></span>
            <Slider
              style={{ marginLeft: 10, marginRight: 10, width: 240 }}
              defaultValue={10}
              min={0}
              max={24}
              onAfterChange={this.log('afterChange')}
            />
          </div>
          <span className="progress-num">{this.state.lightPercent}</span>
        </div>
        {/* 设置语言 */}
        <div className="lang" ref="langMenu">
          <div className="setLang">
            <span className='font'>雅黑</span>
            <label>
              <input onClick={this.SwitchFontStyle.bind(this)} className="mui-switch mui-switch-anim" type="checkbox" />
            </label>
            <span className='font'>宋体</span>
          </div>
          <div className="setProgress">
            <span>字体大小</span>
            <Slider
              style={{ marginLeft: 10, marginRight: 10, width: 240 }}
              defaultValue={10}
              min={0}
              max={24}
              onChange={this.fontProgressHandle()}
              onAfterChange={this.log('afterChange')}
            />
            <span>{this.state.fontPercent}</span>
          </div>

        </div>
        <div className="set">
          <span className={this.state.arrow ? "switch" : ""} onClick={this.switchHandle.bind(this)} />
          <ul className="setCategory">

            <li onClick={this.showDirect.bind(this)}>

              {/* 布局方面最好就是一个span确定图标 一个p标签确定文本 span是来确定字体图标*/}
              <img src={require("../../static/images/content@2x.png")} />
              <span className="text">目录</span>
            </li>
            <li onClick={this.go.bind(this)}>
              <img src={require("../../static/images/note@2x.png")} />
              <span className="text">笔记</span>
            </li>
            <li onClick={this.openLangMenu.bind(this)}>
              <img src={require("../../static/images/font@2x.png")} />
              <span className="text">字体</span>
            </li>
            <li onClick={this.opensetHandle.bind(this)}>
              <img src={require("../../static/images/set@2x.png")} />
              <span className="text">设置</span>
            </li>
          </ul>
        </div>

        <div
          ref="direct"
          className="directory"
          onTouchStart={this.dTouchStartHandle.bind(this)}
          onTouchMove={this.dTouchMoveHandle.bind(this)}
          onTouchEnd={this.dTouchEndHandle.bind(this)}
        >
          <h2>认知潮州</h2>
          {
         ch.map((item,index)=>{
            return(
              <ul key={index}>
              <p
                onClick={() => {
                  this.showCharpter(item.id);
                }}
              >
                {item.title}
              </p>
              <CSSTransition
                in={item.id === this.state.currentId}
                timeout={800}
                classNames="slider"
                onExited={() => {
                  console.log("退出");
                }}
              >
                <div className="dWrpper">
                  {item.essay
                    ? item.essay.map((v,i) => {
                      return <li onClick={this.changeReadContent.bind(this,v.info,v.title)} key={i}>{v.title}</li>;
                    })
                    : " "}
                </div>
              </CSSTransition>
            </ul>
            )
         })
      }
        </div>
      </div>
    );
  }
  componentDidMount() {
    ch = [
      {
        title: "第一章 地理风俗",
        id: 0,
        essay: []
      },
      {
        title: "第二章 名人贤士",
        id: 1,
        essay: []
      },
      {
        title: "第三章 民间艺术",
        id: 2,
        essay: []
      },
      {
        title: "第四章 旅游景点",
        id: 3,
        essay: []
      },
      {
        title: "第五章 教育基地",
        id: 4,
        essay: []
      },
      {
        title: "第六章 潮州名片",
        id: 5,
        essay: []
      },
      {
        title: "第七章 后记",
        id: 6,
        essay: []
      }
    ];
    axios.get("/article", { category: 1 }).then((res) => {
      console.log(res);
      let essay = res.data.essay;
      for (let i = 0; i < essay.length; i++) {
        let charpter = {
          info: essay[i].essay.markdown,
          title: essay[i].essay.title
        }
        console.log( essay[i].essay.markdown);
        switch (essay[i].category) {
          case 1:   
            ch[0].essay.push(charpter)
            break;
          case 2:
            ch[1].essay.push(charpter)
            break;
            case 3:
            ch[2].essay.push(charpter)
            break;
            case 4:
            ch[3].essay.push(charpter)
            break;
            case 5:
            ch[4].essay.push(charpter)
            case 6:
            ch[5].essay.push(charpter)
            case 7:
            ch[6].essay.push(charpter)
            break;
            default:
              break;
        
        }
      }
      console.log(ch)
      this.setState({
         content:essay[0].essay.markdown,
         title:essay[0].essay.title
      })
    })
  
  }
  changeReadContent(info,title){
     this.setState({
        content:info,
        title
     })
  }
  go() {

    this.props.history.push('/note');
  }
  fontProgressHandle() {
    return (value) => {
      console.log(value)

      let fontPercent = parseInt(value / 24 * 100) + "%";
      this.setState({
        fontPercent
      })

      this.refs.readContent.style.fontSize = `${value < 12 ? 12 : value}px`

    }
  }
  log = (name) => {
    return (value) => {
      console.log(`${name}: ${value}`);
    };
  }
  //布局禁忌就是背景图加文字 不好确定 能用图片标签就用图片标签 不用背景图

  //亮度
  lightStartHandle(e) {
    this.setState({
      lightInitclientX: e.touches[0].clientX
    })
    //console.log(this)
  }
  lightMoveHandle(e) {

    let moveX = e.touches[0].clientX - this.state.lightInitclientX;

    //translateX相对的
    if (moveX < 0) {
      moveX = 0
    }
    if (moveX > 208) {
      moveX = 208
    }
    let percent = moveX / 260 + 0.2;
    this.setState({
      lightPercent: parseInt((percent * 100) + 0.2) + "%"
    })
    this.refs.lightProgress.style.width = `${percent * 260}px`
    this.refs.Lbutton.style.transform = `translateX(${moveX}px)`
  }
  lightEndHandle(e) {

  }
  //字体大小
  fontStartHandle(e) {

    console.log(e.touches[0].clientX)
    this.setState({
      fontInitclientX: e.touches[0].clientX
    })

  }

  fontMoveHandle(e) {
    let moveX = e.touches[0].clientX - this.state.fontInitclientX;
    //translateX相对的
    if (moveX < 0) {
      moveX = 0
    }
    if (moveX > 160) {
      moveX = 160
    }
    let percent = moveX / 200 + 0.2;
    // console.log(percent)
    this.setState({
      fontPercent: parseInt((percent * 100) + 0.2) + "%"
    })
    //console.log(this.state.fontPercent)
    //rem 和 px有点恶心 但是我们通过px来 只是微弱的差距而已
    this.refs.fontProgress.style.width = `${percent * 200}px`
    this.refs.Fbutton.style.transform = `translateX(${moveX}px)`

  }
  //  这个函数用来做边界处理
  //放开是没有手指的这个对象的touches
  fontEndHandle(e) {
    //console.log(e.touches)

    let percent = parseInt(this.state.fontPercent) * 0.01
    this.refs.readContent.style.fontSize = `${12 + 6 * percent}px`
    this.setState({
      fontInitclientX: 0
    })

  }
  SwitchFontStyle() {
    this.setState({
      fontStyle: !this.state.fontStyle
    })

    //切换字体
    if (this.state.fontStyle) {
      this.refs.reading.style.fontFamily = "Microsoft YaHei";

    } else {
      this.refs.reading.style.fontFamily = "SimSun";

    }
  }

  //字体菜单
  openLangMenu() {
    if (!this.state.isSwitch) {
      return false
      //this.opensetHandle()

    }
    this.refs.langMenu.style.transition = "all 0.6s"
    if (!this.state.isLang) {
      this.refs.langMenu.style.transform = 'translateY(800px)'

    }
    //打开
    else {
      this.refs.langMenu.style.transform = 'translateY(0)'
    }

    this.setState({
      isLang: !this.state.isLang,
      arrow: !this.state.arrow
    })
  }

  //切换颜色
  //参数位置一定要固定的吗 --已证实就是   ts可以解决这种麻烦事
  changeColor(index, color) {
    console.log(color)
    this.setState({
      colorIndex: index
    })
    //设置背景色
    //document.style.background = `${color.bgColor}`
    document.body.style.background = `${color.bgColor}`
    this.refs.reading.style.color = `${color.fontColor}`
    //this.refs.reading.style.background = `${color.bgColor}`
  }
  //菜单开关
  opensetHandle() {
    if (!this.state.isLang) {
      return false
      // this.openLangMenu()

      //感觉是被react合起来一起setState
    }
    this.refs.setMenu.style.transition = "all 0.6s";
    if (!this.state.isSwitch) {

      this.refs.setMenu.style.transform = 'translateY(800px)';

    }
    //打开
    else {
      this.refs.setMenu.style.transform = 'translateY(0)';
    }
    this.setState({
      isSwitch: !this.state.isSwitch,
      arrow: !this.state.arrow
    })
  }
  switchHandle() {
    //如果arrow为false时候return
    if (!this.state.arrow) {
      return false
    }
    //关
    this.refs.setMenu.style.transition = "all 0.6s"
    if (!this.state.isSwitch) {

      this.refs.setMenu.style.transform = 'translateY(800px)'
      this.setState({
        isSwitch: !this.state.isSwitch
      })
    }
    if (!this.state.isLang) {
      this.refs.langMenu.style.transform = 'translateY(800px)'
      this.setState({
        isLang: !this.state.isLang,

      })
    }
    this.setState({
      arrow: false
    })
  }

  showCharpter(id) {
    //用数据改变而不是通过操作dom ---通常操作数据可能跟css配合 效果刚好
    if (this.state.currentId === id) {
      this.setState(() => ({ currentId: "hidden" }));
      return;
    }
    this.setState(() => ({
      currentId: id
    }));
  }
  dTouchStartHandle(e) {
    //console.log(e);
    //console.log(e.touches[0]);
    let TouchstartX = e.touches[0].clientX;
    // 异步this问题
    this.setState(() => ({
      startX: TouchstartX
    }));
  }
  dTouchMoveHandle(e) {
    //移动多少 就跟着移动多少
    let moveX = e.touches[0].clientX - this.state.startX;
    if (moveX > 0) {
      moveX = 0;
    }
    //console.log(moveX);
    this.refs.direct.style.transition = "all 0.3s";
    this.refs.direct.style.transform = `translateX(${moveX}px)`;
  }
  dTouchEndHandle(e) {
    //踩坑
    // console.log(e.changedTouches[0]);
    let endX = e.changedTouches[0].clientX - this.state.startX;
    console.log(endX);
    if (endX > -50) {
      this.refs.direct.style.transition = "all 0.5s";
      this.refs.direct.style.transform = `translateX(0)`;
      return;
    }
    this.refs.direct.style.transition = "all 0.5s";
    this.refs.direct.style.transform = `translateX(-1200px)`;
    this.setState(() => ({
      showDirect: false
    }));
  }
  showDirect() {
    if (!this.state.showDirect) {
      this.refs.direct.style.transition = "all 0.8s";
      this.refs.direct.style.transform = `translateX(0)`;
    } else {
      this.refs.direct.style.transition = "all 0.8s";
      this.refs.direct.style.transform = `translateX(-1200px)`;
    }

    this.setState(() => ({
      showDirect: !this.state.showDirect
    }));
  }
}

export default Charpter;

